				let data ={
					keyword: val
				}
				const res = await this.$http.get('shop/specification_query',{data: data})
				console.log(res)
				this.list = res.data.data.list
                this.$emit('input',this.name)

			},
			select(item) {
				this.backName = item;
				this.name = item;
				this.list = [];
				this.$emit('select', item);
				uni.hideKeyboard()
			},
